import classNames from 'classnames';
import React from 'react';
import { Form, Field } from 'react-final-form';
import renderControlInput from './renderControlInput';
import renderRadioButtons from './renderRadioButtons';

const QuoteForm = ({ copy, notification, onClick, onSubmit }) => {
  return [
    notification && (
      <div
        className={classNames({ notification: true, 'is-success': true })}
        key="notification"
      >
        <button className="delete" onClick={onClick} />
        {copy['thanks-for-filling-out-quote-form']}
      </div>
    ),
    <Form
      initialValues={{
        key: 'quote',
        'audio-quality': 'difficult',
        'date-required-for-completion': '',
        format: 'verbatim',
        'length-of-project-hours': '',
        'length-of-project-minutes': '',
        'length-of-project-seconds': '',
        'time-coded': 'yes',
        'time-required-for-completion': ''
      }}
      key="form"
      onSubmit={onSubmit}
      render={({ form, handleSubmit, submitting, values }) => (
        <form
          onSubmit={event => {
            event.preventDefault();
            handleSubmit(event).then(() => {
              form.reset();
            });
          }}
        >
          <div className="field">
            <label className="label is-serif">
              {copy['length-of-project']}
            </label>
          </div>
          <div className="field is-grouped is-grouped-multiline">
            <Field name="length-of-project-hours">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['length-of-project-hours-placeholder'],
                  submitting,
                  'number'
                )
              }
            </Field>
            <Field name="length-of-project-minutes">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['length-of-project-minutes-placeholder'],
                  submitting,
                  'number'
                )
              }
            </Field>
            <Field name="length-of-project-seconds">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['length-of-project-seconds-placeholder'],
                  submitting,
                  'number'
                )
              }
            </Field>
          </div>
          <div className="field">
            <label
              className="label is-serif"
              dangerouslySetInnerHTML={{
                __html: copy['delivery-date-and-time']
              }}
            />
          </div>
          <div className="field is-grouped is-grouped-multiline">
            <Field name="date-required-for-completion">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['date-required-for-completion-placeholder'],
                  submitting,
                  'date'
                )
              }
            </Field>
            <Field name="time-required-for-completion">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  null,
                  meta,
                  copy['time-required-for-completion-placeholder'],
                  submitting,
                  'time'
                )
              }
            </Field>
          </div>
          {renderRadioButtons(
            copy['time-coded'],
            [
              { name: 'time-coded', value: 'yes', label: copy['yes'] },
              { name: 'time-coded', value: 'no', label: copy['no'] }
            ],
            submitting
          )}
          {renderRadioButtons(
            copy['format'],
            [
              { name: 'format', value: 'clean', label: copy['clean'] },
              { name: 'format', value: 'verbatim', label: copy['verbatim'] }
            ],
            submitting
          )}
          {renderRadioButtons(
            copy['audio-quality'],
            [
              { name: 'audio-quality', value: 'clear', label: copy['clear'] },
              {
                name: 'audio-quality',
                value: 'difficult',
                label: copy['difficult']
              }
            ],
            submitting
          )}
          <div className="field">
            <div className="control">
              <button
                className={classNames({
                  button: true,
                  'is-loading': submitting,
                  'is-seafoam-blue': true
                })}
                disabled={submitting}
              >
                {copy['submit-quote']}
              </button>
            </div>
          </div>
        </form>
      )}
      validate={values => {
        const errors = {};
        if (!values['length-of-project-hours']) {
          errors['length-of-project-hours'] = copy['required'];
        }
        if (!values['length-of-project-minutes']) {
          errors['length-of-project-minutes'] = copy['required'];
        }
        if (!values['length-of-project-seconds']) {
          errors['length-of-project-seconds'] = copy['required'];
        }
        if (!values['date-required-for-completion']) {
          errors['date-required-for-completion'] = copy['required'];
        }
        if (!values['time-required-for-completion']) {
          errors['time-required-for-completion'] = copy['required'];
        }
        return errors;
      }}
    />
  ];
};

export default QuoteForm;



