import classNames from 'classnames';
import React from 'react';
import isDanger from './isDanger';

const renderControlInput = (
  input,
  label,
  meta,
  placeholder,
  submitting,
  type = 'text'
) => (
  <div className="control">
    {label && <label className="label is-serif">{label}</label>}
    <input
      {...input}
      className={classNames({ input: true, 'is-danger': isDanger(meta) })}
      disabled={submitting ? true : false}
      placeholder={placeholder}
      type={type}
    />
    {isDanger(meta) && <p className="help is-danger">{meta.error}</p>}
  </div>
);

export default renderControlInput;



