import classNames from 'classnames';
import React from 'react';
import { Form, Field } from 'react-final-form';
import isDanger from './isDanger';
import renderControlInput from './renderControlInput';

const ContactForm = ({ copy, notification, onClick, onSubmit }) => {
  return [
    notification && (
      <div
        className={classNames({ notification: true, 'is-success': true })}
        key="notification"
      >
        <button className="delete" onClick={onClick} />
        {copy['thanks-for-filling-out-contact-form']}
      </div>
    ),
    <Form
      initialValues={{
        email: '',
        key: 'contact',
        message: '',
        name: '',
        subject: ''
      }}
      key="form"
      onSubmit={onSubmit}
      render={({ form, handleSubmit, submitting, values }) => (
        <form
          onSubmit={event => {
            event.preventDefault();
            handleSubmit(event).then(() => {
              form.reset();
            });
          }}
        >
          <div className="field">
            <Field name="name">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['name'],
                  meta,
                  copy['name-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          <div className="field">
            <Field name="email">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['email'],
                  meta,
                  copy['email-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          <div className="field">
            <Field name="subject">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['subject'],
                  meta,
                  copy['subject-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          <div className="field">
            <Field name="message">
              {({ input, meta }) => (
                <div className="field">
                  <label className="label is-serif">{copy['message']}</label>
                  <div className="control">
                    <textarea
                      {...input}
                      className={classNames({
                        textarea: true,
                        'is-danger': isDanger(meta)
                      })}
                      disabled={submitting ? true : false}
                      placeholder={copy['message-placeholder']}
                    />
                    {isDanger(meta) && (
                      <p className="help is-danger">{meta.error}</p>
                    )}
                  </div>
                </div>
              )}
            </Field>
          </div>
          <div className="field">
            <div className="control">
              <button
                className={classNames({
                  button: true,
                  'is-loading': submitting,
                  'is-seafoam-blue': true
                })}
                disabled={submitting}
              >
                {copy['send-message']}
              </button>
            </div>
          </div>
        </form>
      )}
      validate={values => {
        const errors = {};
        if (!values.email) {
          errors.email = copy['required'];
        }
        if (!values.message) {
          errors.message = copy['required'];
        }
        if (!values.name) {
          errors.name = copy['required'];
        }
        if (!values.subject) {
          errors.subject = copy['required'];
        }
        return errors;
      }}
    />
  ];
};

export default ContactForm;



