import React from 'react';
import { Field } from 'react-final-form';

const renderRadioButtons = (label, radios, submitting) => {
  return (
    <div className="field">
      <label className="label is-serif">{label}</label>
      <div className="control">
        {radios.map(({ name, value, label }) => (
          <label className="radio" key={value}>
            <Field name={name} type="radio" value={value}>
              {({ input, meta }) => {
                return (
                  <input
                    {...input}
                    disabled={submitting ? true : false}
                    type="radio"
                  />
                );
              }}
            </Field>{' '}
            {label}
          </label>
        ))}
      </div>
    </div>
  );
};

export default renderRadioButtons;



