import classNames from 'classnames';
import React from 'react';
import { Form, Field } from 'react-final-form';
import renderControlInput from './renderControlInput';
import renderRadioButtons from './renderRadioButtons';

const OrderForm = ({ copy, notification, onClick, onSubmit }) => {
  return [
    notification && (
      <div
        className={classNames({ notification: true, 'is-success': true })}
        key="notification"
      >
        <button className="delete" onClick={onClick} />
        {copy['thanks-for-filling-out-order-form']}
      </div>
    ),
    <Form
      initialValues={{
        company: '',
        'delivery-time': 'rush',
        email: '',
        key: 'order',
        name: '',
        'phone-number': '',
        'project-name': ''
      }}
      key="form"
      onSubmit={onSubmit}
      render={({ form, handleSubmit, submitting, values }) => (
        <form
          onSubmit={event => {
            event.preventDefault();
            handleSubmit(event).then(() => {
              form.reset();
            });
          }}
        >
          <div className="field">
            <Field name="name">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['name'],
                  meta,
                  copy['name-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          <div className="field">
            <Field name="company">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['company'],
                  meta,
                  copy['company-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          <div className="field">
            <Field name="email">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['email'],
                  meta,
                  copy['email-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          <div className="field">
            <Field name="phone-number">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['phone-number'],
                  meta,
                  copy['phone-number-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          <div className="field">
            <Field name="project-name">
              {({ input, meta }) =>
                renderControlInput(
                  input,
                  copy['project-name'],
                  meta,
                  copy['project-name-placeholder'],
                  submitting
                )
              }
            </Field>
          </div>
          {renderRadioButtons(
            copy['delivery-time'],
            [
              {
                name: 'delivery-time',
                value: 'standard',
                label: copy['standard']
              },
              { name: 'delivery-time', value: 'rush', label: copy['rush'] },
              {
                name: 'delivery-time',
                value: 'expedited',
                label: copy['expedited']
              }
            ],
            submitting
          )}
          <div className="field">
            <div className="control">
              <button
                className={classNames({
                  button: true,
                  'is-loading': submitting,
                  'is-seafoam-blue': true
                })}
                disabled={submitting}
              >
                {copy['submit-order']}
              </button>
            </div>
          </div>
        </form>
      )}
      validate={values => {
        const errors = {};
        if (!values.company) {
          errors.company = copy['required'];
        }
        if (!values.email) {
          errors.email = copy['required'];
        }
        if (!values.name) {
          errors.name = copy['required'];
        }
        return errors;
      }}
    />
  ];
};

export default OrderForm;



